<template>
    <div class="main">
        <el-container>
            <el-header>
                <h1>公务员测评系统</h1>
                <div>
                    <el-radio-group :disabled="true">
                        <el-radio><label>好</label><span>（全部工作质量都能达到要求）</span></el-radio>
                        <el-radio><label>较好</label><span>（工作质量整体令人满意，极少出现问题）</span></el-radio>
                        <el-radio><label>一般</label><span>（工作质量基本令人满意，偶尔出现问题）</span></el-radio>
                        <el-radio><label>差</label><span>（工作质量不很让人放心，时常出现问题）</span></el-radio>
                    </el-radio-group>
                </div>
            </el-header>
            <el-main>
                <org-manager ref="orgManager" v-if="managers.length > 0" :users="managers"></org-manager>
                <org-member ref="orgMember" v-if="ownMembers.length > 0" :users="ownMembers"></org-member>
                <other-org-member ref="otherOrgMember" v-if="otherMembers.length > 0" :users="otherMembers"></other-org-member>
            </el-main>
            <el-footer>
                <el-button @click="onSubmit">提交</el-button>
            </el-footer>
        </el-container>
        <el-dialog title="提示" :visible="showDlg">
            <span style="text-align:center;">{{dlgText}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="onSure">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import AssessGroup from "../components/AssessGroup";
    import AssessMain from "../components/AssessMain";
    import OrgManager from "../components/orgManager";
    import OrgMember from "../components/orgMember";
    import OtherOrgMember from "../components/otherOrgMember";
    export default {
        components: {
            AssessGroup,
            AssessMain,
            OrgManager,
            OrgMember,
            OtherOrgMember
        },
        data () {
            return {
                resource: {},
                managers: [],
                ownMembers: [],
                otherMembers: [],
                userId: "",
                dlgText: "",
                submiting: false,
                showDlg: false
            }
        },
        created: function() {
            var reg = new RegExp("(^id)" + "=([^&]*)(&|$)");
            let r = window.location.search.substr(1).match(reg);
            if (r != null) {
                this.userId = unescape(r[2]);
                this.$http({
                url: this.$http.adornUrl("resource/getresourcebyid"),
                data: this.$http.adornData({
                    id: this.userId//这里设置用户id
                }),
                method: 'post'
            }).then((res) => {
                this.resource = res.data.data.resource;
                this.managers = res.data.data.managers;
                this.ownMembers = res.data.data.ownMembers;
                this.otherMembers = res.data.data.otherMembers;
            }).catch((res) => {
                console.log("fail");
                console.log(res);
            })
            }
            
        },
        methods: {
            onSubmit: function() {
                if (this.submiting) {
                    this.dlgText = "正在提交，请勿重复操作";
                    this.showDlg = true;
                    return;
                }
                this.submiting = true;
                let self = this;
                if (this.$refs["orgManager"].check() && this.$refs["orgMember"].check() && this.$refs["otherOrgMember"].check()) {
                    let data = this.parseData(this.managers, this.$refs["orgManager"].data)
                    .concat(this.parseData(this.ownMembers, this.$refs["orgMember"].data))
                    .concat(this.parseData(this.otherMembers, this.$refs["otherOrgMember"].data));
                    this.$http({
                        url: this.$http.adornUrl("assess/save"),
                        data: this.$http.adornData({
                            assessId: this.resource.id,
                            result: JSON.stringify(data)
                        }),
                        method: 'post'
                    }).then((res) => {
                        console.log(res);
                        self.submiting = false;
                        self.dlgText = "提交成功！";
                        self.showDlg = true;
                    }).catch((res) => {
                        console.log("failed");
                    })
                }
            },
            parseData: function(users ,data) {
                if (users.length == data.length) {
                    let result = [];
                    for (let i = 0; i < users.length; i++) {
                        result.push({userId: users[i].id, data: data[i]});
                    }
                    return result;
                }
            },
            onSure: function() {
                this.showDlg = false;
                if (!this.submiting) {
                    window.opener = null;
                    window.close();
                }
            }
        },
        mounted: function() {
            // console.log(window.location);
            // console.log(this.$route);
        }
    }
</script>
<style>
    .main {
        width: 100%;
        height: 100%;
        background-image: url('/src/assets/login.png');
    }
</style>


