<template>
    <div>
        <h2>处室负责人</h2>
        <!-- <el-link href="./static/test.pdf">test.pdf</el-link> -->
        <!-- <pdf src="./static/test.pdf"></pdf> -->
        <div>
            <el-card v-for="(user, index) in users" :key="index">
                <div slot="header">
                    <span>{{index + 1}}.{{user.lastname}}</span>
                    <div style="margin-top:10px;">
                        <el-link icon="el-icon-paperclip" :href="'./static/' + $store.state.pdfPrefix1 + user.lastname + '.pdf'" target="_blank" style="margin-right:10px;">{{$store.state.pdfPrefix1 + user.lastname}}.pdf</el-link>
                        <el-link icon="el-icon-paperclip" :href="'./static/' + $store.state.pdfPrefix2 + user.lastname + '.pdf'" target="_blank">{{$store.state.pdfPrefix2 + user.lastname}}.pdf</el-link>
                    </div>
                </div>
                <div>
                    <el-radio-group v-model="data[index]" @change="onChange(index, user.id)">
                        <el-radio :label="0">好</el-radio>
                        <el-radio :label="1">较好</el-radio>
                        <el-radio :label="2">一般</el-radio>
                        <el-radio :label="3">较差</el-radio>
                    </el-radio-group>
                </div>
            </el-card>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                zeroCount: 0,
                data: []
            }
        },
        props: {
            users: {
                type: Array,
                value: []
            }
        },
        mounted: function() {
        },
        methods: {
            onChange: function(index, userId) {
                if (this.data[index] == 0) {
                    if (this.zeroCount < Math.ceil(this.users.length * 0.4)) {
                        this.zeroCount++;
                    } else {
                        this.$message("小组评测结果为\"好\"的人员最多占该小组人数的40%，请重新评测！");
                        this.$set(this.data, index, -1);
                    }
                }
            },
            check: function() {
                if(this.data.length < this.users.length || this.data.indexOf(-1) > -1) {
                    this.$message("处室负责人还有人员等待评测，请评测完成后点击提交！");
                    return false;
                }
                return true;
            }
        }
        
    }
</script>


