import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        mainTab: 1,
        pdfPrefix1: "2020年度公务员平时考核指标（个性指标部分）-",
        pdfPrefix2: "第三季度工作总结-"
    },
    mutations: {
        updateMainTab: function(state, tab) {
            state.mainTab = tab;
        }
    }
})

export default store;


