<template>
    <div>
        <el-menu default-active="1">
            <el-menu-item index="1" @click="changeMainTab(1)">
                <span slot="title">处室负责人</span>
            </el-menu-item>
            <el-menu-item index="2" @click="changeMainTab(2)">
                <span slot="title">分管处室工作人员</span>
            </el-menu-item>
            <el-menu-item index="3" @click="changeMainTab(3)">
                <span slot="title">其他处室工作人员</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>
<script>
    export default {
        data() {
            return {

            }
        },
        methods: {
            changeMainTab: function(tabIndex) {
                this.$store.commit("updateMainTab", tabIndex);
            }
        }
    }
</script>


