<template>
    <div>
        <org-manager v-if="mainTab == 1"></org-manager>
        <org-member v-else-if="mainTab == 2"></org-member>
        <other-org-member v-else-if="mainTab == 3"></other-org-member>
    </div>
</template>
<script>
import OrgManager from './orgManager'
import OrgMember from './orgMember'
import OtherOrgMember from './otherOrgMember'
export default {
    data() {
        return {
        }
    },
    computed: {
        mainTab: {
            get() { return this.$store.state.mainTab },
            set(val) {this.$store.commit('updateMainTab', val) }
        }
    },
    components: {                           
        OrgManager,
        OrgMember,
        OtherOrgMember
    }
}
</script>


