import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/views/main'
import OrgManager from '@/components/orgManager'
import OrgMember from '@/components/orgManager'
import OtherOrgMember from '@/components/otherOrgMember'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      props: (route) => ({id: 121}),
      component: Main
    },
    {
        path: '/orgManager',
        name: 'orgManager',
        component: OrgManager
    },
    {
        path: '/orgMember',
        name: 'orgMember',
        component: OrgMember
    },
    {
        path: '/otherOrgMember',
        name: 'otherOrgMember',
        component: OtherOrgMember
    }
  ]
})



